import React, { useState } from 'react';
import InputTextField from './styled/InputTextField'
import SubmitButton from './styled/SubmitButton'
import db from '../firebaseConfig'
import 'firebase/database'
import AddChoreForm from './styled/AddChoreForm'

const AddChore = props => {
    const [ taskInputValue, setTaskInputValue ] = useState('')
    const [ personInputValue, setPersonInputValue ] = useState('')

    const handleSubmit = e => {
        e.preventDefault()
        if ( taskInputValue && personInputValue ) {
            db.collection('chores').add({
                task: taskInputValue,
                person: personInputValue,
                timestamp: new Date(),
            }).then(() => {
                console.log('sukces')
            }).catch(error => {
                console.log(error)
            })
            setTaskInputValue('')
            setPersonInputValue('')
        }
    }

    return (
        <AddChoreForm onSubmit={handleSubmit}>
            <InputTextField
            autoComplete='off'
            id="standard-full-width"
            label="add task"
            placeholder="type here"
            value={taskInputValue}
            onChange={e => setTaskInputValue(e.target.value)}
            margin="none"
            InputLabelProps={{
                shrink: true,
            }}
            />
            <InputTextField
            autoComplete='off'
            id="standard-full-width"
            label="who's doing it"
            value={personInputValue}
            onChange={e => setPersonInputValue(e.target.value)}
            placeholder="type here"
            margin="none"
            InputLabelProps={{
                shrink: true,
            }}
            />
            <SubmitButton type='submit' style={{marginRight: '8px',}}>
                Submit
            </SubmitButton>
      </AddChoreForm>
    )
}

export default AddChore;