import { useState, useEffect } from 'react';
import db from '../../firebaseConfig'

const useListenForChoreData = () => {
    const [ choresData, setChoresData ] = useState( [] )
	const [ isLoading, setIsLoading ] = useState(false);
    const [ isError, setIsError ] = useState(false);
    
    let unsubscribe;
    useEffect( () => {
		const listenForChoreData = async () => {
			setIsLoading(true)
			unsubscribe = await db.collection("chores").onSnapshot( snapshot => {
                setIsError(false);
				const fetchedChores = snapshot.docs.map(e => {
					return {
						id: e.id,
						task: e.data().task,
						person: e.data().person,
						timestamp: e.data().timestamp,
					}
                })
                setChoresData(fetchedChores);
				setIsLoading(false);
			}, error => setIsError(true) )
		}
        listenForChoreData();
        
        return ( () => unsubscribe() )
	}, [])
	
	// useEffect( () => {
	// 	const listenForChanges = async () => {
	// 		db.collection('chores').onSnapshot(snapshot => {
	// 			setListener(!listener);
	// 			console.log(listener)
	// 		})
	// 	}
	// 	// listenForChanges();
	// }, [])
    
    return { choresData, isLoading, isError, setChoresData }
}

export default useListenForChoreData;
