import React from 'react';
import ChoreItem from './ChoreItem'
import NoChores from './NoChores'

const ChoreList = props => {
    // const style = {
    //     wrapper: {
    //         maxHeight: '240px',
    //         overflow: 'scroll',
    //     },
    // }

    let listOfChores = props.data ? props.data.map( (e, i) => {
        
        return (
            <ChoreItem task={e.task} person={e.person} key={e.id} odd={i % 2} id={e.id} key={e.id} timestamp={e.timestamp}/>
        )
    }) : null;

    listOfChores = listOfChores && listOfChores.sort( (a, b) => {
        console.log(a.props.task)
        return b.props.timestamp.seconds - a.props.timestamp.seconds
    })

    return (
        <>
            {listOfChores.length ? listOfChores : <NoChores />}
        </>
    );
}

export default ChoreList;